<template>
    <div v-for="(product, key) in products.data" :key="key" class="row col-10 radius bg-secondary m-auto mb-3">

        <div class="col-11 m-auto m-sm-0 col-sm-5">
            <img :src="images(product)[0]">
        </div>

        <div class="col-11 col-sm-6">
            <div class="col-12 m-auto mt-3 row">
                <div class="col-6">
                    <span class="h5"><strong>Product name:</strong></span>
                </div>
                <div class="col-6">
                    <span class="h5"><i>{{ product.title }}</i></span>
                </div>

            </div>
            <div class="col-12 m-auto mt-3 row">
                <div class="col-6">
                    <span class="h5"><strong>Product category:</strong></span>
                </div>
                <div class="col-6">
                    <span class="h5"><i>{{ product.category.title }}</i></span>
                </div>

            </div>
            <div class="col-12 m-auto mt-3 row">
                <div class="col-6">
                    <span class="h5"><strong>Product price:</strong></span>
                </div>
                <div class="col-6">
                    <span class="h5"><i>{{ product.price + '$' }}</i></span>
                </div>

            </div>
            <div class="col-12 m-auto mt-3 row">
                <div class="col-6">
                    <span class="h5"><strong>Product Quantity:</strong></span>
                </div>
                <div class="col-6">
                    <span class="h5"><i>{{ product.quantity }}</i></span>
                </div>
                <p  class="display-6 mt-3 text-center"><strong>Product tags</strong></p>
                <div v-if="product.tags.length" class="col-6">
                    <span  v-for="(tag,k) in product.tags" :key="k"  class="display-6 p-2 m-2 border radius border-dark"><i>{{ tag.title }}</i></span>
                </div>
                <div v-else class="col-12 text-center">
                    <span  class="display-6 p-2 m-2 "><i>Empty</i></span>
                </div>

            </div>
            <div class="col-12 row mb-5">
                <div class="col-3 m-auto mt-5 text-danger text-center">
                    <router-link :to="{ name: 'showProduct', params: {id: product.id}}" class="text-dark">
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor"
                             class="bi bi-eye-fill" viewBox="0 0 16 16">
                            <path d="M10.5 8a2.5 2.5 0 1 1-5 0 2.5 2.5 0 0 1 5 0z"/>
                            <path
                                d="M0 8s3-5.5 8-5.5S16 8 16 8s-3 5.5-8 5.5S0 8 0 8zm8 3.5a3.5 3.5 0 1 0 0-7 3.5 3.5 0 0 0 0 7z"/>
                        </svg>
                    </router-link>
                </div>
                <div v-if="like" class="col-3 m-auto mt-5 text-danger text-center">
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor"
                         class="bi bi-heart" viewBox="0 0 16 16">
                        <path
                            d="m8 2.748-.717-.737C5.6.281 2.514.878 1.4 3.053c-.523 1.023-.641 2.5.314 4.385.92 1.815 2.834 3.989 6.286 6.357 3.452-2.368 5.365-4.542 6.286-6.357.955-1.886.838-3.362.314-4.385C13.486.878 10.4.28 8.717 2.01L8 2.748zM8 15C-7.333 4.868 3.279-3.04 7.824 1.143c.06.055.119.112.176.171a3.12 3.12 0 0 1 .176-.17C12.72-3.042 23.333 4.867 8 15z"/>
                    </svg>
                    <span class="ms-2">{{ product.likes.length }}</span>
                </div>
                <div v-else class="col-3 m-auto mt-5 text-danger text-center">
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor"
                         class="bi bi-heart-fill" viewBox="0 0 16 16">
                        <path fill-rule="evenodd"
                              d="M8 1.314C12.438-3.248 23.534 4.735 8 15-7.534 4.736 3.562-3.248 8 1.314z"/>
                    </svg>
                    <span class="ms-2">{{ product.likes.length }}</span>
                </div>
            </div>
        </div>
        <pagination :callback="getResults" :pagination="products" :size="'small'">

        </pagination>
    </div>
</template>

<script>
import PaginationComponent from "@/components/PaginationComponent";
import axios from "axios";

export default {
    name: "ShowProducts",

    components: {
        Pagination: PaginationComponent,
    },

    data() {
        return {
            id: this.$route.params.id,
            products: {},
            like: true,
        }
    },

    computed: {
        url() {
            return this.$store.getters.getUrl + '/api/categories/';
        },


    },


    methods: {
        images(product) {
            return  JSON.parse(product.images);
        },

        query() {
            axios.get(this.url + this.id)
                .then(r => {
                    console.log(r)
                    this.products = r.data;
                })
                .catch(err => {
                    console.log(err);
                });
        },

        getResults(page = 1) {
            axios.get(this.url + '?page=' + page)
                .then(r => {
                    this.products = r.data;
                });
        },

    },

    watch: {
        '$route.params.id': {
            immediate: true,
            handler() {
                this.id=this.$route.params.id,
                this.query();
            },
        },
    },

    created() {
        this.query();
    },


}
</script>

<style scoped>
img {
    width: 100%;
    padding: 3px;
    height: 300px;
    border-radius: 10px;
}
</style>
